a {
  text-decoration: none;
  color: inherit;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-weight: normal;
}

.container {
  width: 100%;
  min-height: 100vh;
  background-image: -webkit-gradient(linear, left bottom, right top, from(#c44b8f), color-stop(#6a2b80), to(#482478));
  background-image: -webkit-linear-gradient(left bottom, #c44b8f, #6a2b80, #482478);
  background-image: -o-linear-gradient(left bottom, #c44b8f, #6a2b80, #482478);
  background-image: linear-gradient(to right top, #c44b8f, #6a2b80, #482478);
  background-attachment: fixed;
  font-family: 'Open Sans', sans-serif;
}



