@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {opacity: 1;}
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {opacity: 1;}
}

.titleContainer {
  min-height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.title {
  width: 100%;
  text-align: center;
  background-color: #482478;
  color: white;
  font-size: 8em;
  margin-bottom: 50px;
  letter-spacing: 40px;
  text-indent: 20px; /* counter-act letter spacing for centering */
}

.titleCursive {
  font-family: 'Kavoon', cursive;
  text-shadow: 8px 8px 0 rgba(0, 0, 0, 0.25);
}

.titleSans {
  font-family: 'Open Sans', sans-serif;
  text-shadow: -8px -8px 0 rgba(0, 0, 0, 0.25);
}

.chooseCat  {
  width: 600px;
  padding: 20px;
  margin: auto;
  text-align: center;
  background: none;
  border: 3px solid white;
  font-size: 1.75em;
  color: white;
  letter-spacing: 7px;
  text-indent: 1.5px;
  cursor: pointer;
  -webkit-transition: width 175ms ease-out;
  -o-transition: width 175ms ease-out;
  transition: width 175ms ease-out;
}

.chooseCat:hover {
  width: 100%;
  background: white;
  color: #482478;
}

@media only screen and (max-width: 1224px) {
  .title {
    font-size: 6em;
    letter-spacing: 30px;
    text-indent: 15px;
  }
}

@media only screen and (max-width: 768px) {
  .title {
    font-size: 4em;
    letter-spacing: 20px;
    text-indent: 10px;
  }
  .chooseCat {
    font-size: 1.25em;
    width: 400px;
  }
}

@media only screen and (max-width: 480px) {
  .title {
    font-size: 2em;
    letter-spacing: 10px;
    text-indent: 5px;
  }

  .titleCursive {
    text-shadow: 4px 4px 0 rgba(0, 0, 0, 0.25);
  }

  .titleSans {
    text-shadow: -4px -4px 0 rgba(0, 0, 0, 0.25);
  }

  .chooseCat {
    width: 75%;
    min-width: 275px;
    padding: 10px;
    font-size: 1em;
    letter-spacing: 2px;
  }

}



